import { CircularProgress, Container, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField, Typography } from '@mui/material'
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { CoinList } from '../Api/Api'
import { CryptoState } from './CryptoContext'

export const CoinsTable = () => {
    const [coins, setcoins] = useState([])
    const [loading, setloading] = useState(false)

    const {currency,symbol} = CryptoState();
    const fetchCoins = async ()=>{
        setloading(true)
        const {data} = await axios.get(CoinList(currency))
        setcoins(data)
        setloading(false)
    }
    useEffect(() => {
      fetchCoins();
    }, [currency])
    // console.log(coins)
    const [searched, setsearched] = useState("")
    
    const handleSearch =  ()=>{
        return coins.filter((coin)=>{
            if (searched==="") {
                return coin;     
            
            
        } else {
            
            return coin.name.toLowerCase().startsWith(searched.toLowerCase()) || coin.symbol.toLowerCase().startsWith(searched.toLowerCase());
                
        }
        })
    }
   
        
    
        
    
    
    
  return (
    <Container style={{textAlign:"center"}}>
        <Typography variant='h5'>Crypto currencies by Market Cap</Typography>
        <TextField style={{width:"100%"}} 
        label="Search for Currency" 
        variant="outlined" 
        value={searched}
        onChange={(e)=>{(setsearched(e.target.value))}}
        sx={{backgroundColor:"whitesmoke",color:"white"}}>

        </TextField>
        <TableContainer>
            {loading?(
                <CircularProgress sx={{backgroundColor:"yellow"}} /> 
                
            
            ):
            <Table>
                <TableHead>
                    <TableRow>
                    {["Coin","Price","24hr Change","MarketCap"].map((item,index)=>{
                        {/* console.log(index); */}
                        return(
                       <TableCell><div style={{color:"white"}}>{item}</div></TableCell>
                       )
                    })}
                        
                    </TableRow>
                </TableHead>
                    
                <TableBody>
                    {handleSearch().map((coin)=>{
                        let profit = coin.price_change_percentage_24h>0;
                        
                        return(
                            <TableRow 
                            >
                            <TableCell component="th" scope="row" style={{color:"white"}}>
                                <img src={coin.image} style={{height:"30px"}} alt={coin.name} />
                                <div style={{display:"flex",flexDirection:"column",fontSize:"22"}}>
                                <span style={{textTransform:"uppercase"}}>{coin.symbol}</span>
                                <span>{coin.name}</span>

                                </div>
                                </TableCell>
                                <TableCell style={{color:"white",alignItems:"center"}}>
                                     {symbol} {coin.current_price.toFixed(2)}
                                </TableCell>
                                <TableCell align="right" style={{color:profit>0?"green":"red"}}>
                                    
                                    {profit && "+"}{coin.price_change_percentage_24h.toFixed(2)}%
                                </TableCell>
                                <TableCell align="right" style={{color:"white"}}>
                                    {symbol}{coin.market_cap}M</TableCell>
                            </TableRow>
                            
                        )
                    })}
                </TableBody>
            </Table>
            }
        </TableContainer>
    </Container>
  )
}
